export default {
	data() {
		return {
			defaultOptions: {
				isVisible: false, //是否显示弹窗

				title: '', //标题
				content: '', //内容
				contentStyle: '', //内容样式
				style: null, //自定义弹窗样式
				skin: '', //弹窗风格
				icon: '', //弹窗图标
				xclose: false, //自定义关闭按钮

				shade: true, //遮罩层
				shadeClose: true, //点击遮罩关闭
				opacity: '', //遮罩透明度
				time: 0, //自动关闭秒数
				end: null, //销毁弹窗回调函数

				anim: 'scaleIn', //弹窗动画  scaleIn(默认) | fadeIn | shake | top | right | bottom | left
				position: '', //弹窗位置  top | right | bottom | left

				btns: null, //弹窗按钮
			},
			opts: {},
			timer: null,
			loginCode: '',
			inviterCode: '',
			isUrl: true,
		};
	},
	methods: {
		show(args) {
			this.loginCode = args.loginCode
			this.opts = Object.assign({}, this.defaultOptions, args, {
				isVisible: true
			})
			this.isUrl = args.isUrl
			//自动关闭
			// if(this.opts.time) {
			//     this.timer = setTimeout(() => {
			//        this.close()
			//     }, this.opts.time * 1000)
			// }
		},
		getPhoneNumber(e) {
			console.log(e)
			// 加密数据
			var encryptedData = e.detail.encryptedData;
			// 解密方式
			var iv = e.detail.iv;
			if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
				console.log('他取消掉了');
			} else {
				let _this = this
				_this.$getJson('/api/v2/vue/stPlusShop/common/getMobileAndOpenId.jsp', {
					data: JSON.stringify({
						decryptData: encryptedData,
						code: _this.loginCode,
						iv: iv
					})
				}, 'POST', res => {
					if (res.data.wxUserInfoByPage != null) {
						uni.setStorageSync('openid', res.data.wxUserInfoByPage.openid);
					}
					if (res.data.mobile != null) {
						_this.rigister(res.data.mobile);
						uni.setStorageSync('mobile', res.data.mobile);
					}
					_this.$getJson('/api/v2/vue/sqPlus/index/login.jsp', {data:JSON.stringify(
						{
							mobile:res.data.mobile,
							openid:res.data.wxUserInfoByPage.openid,
							code:_this.loginCode
						})}, 'POST',(data)=>{
							uni.setStorageSync('SESSION',data.data)
						})


				});
			}
		},
		rigister(mobile) {
			//注册
			this.$getJson('/api/v2/vue/ydd/user/register.jsp', {
				data: JSON.stringify({
					mobile: mobile,
					inviterCode: this.inviterCode
				})
			}, 'POST', res => {
				this.$emit("changes", res.data);
				uni.setStorageSync('userInfo', res.data);
			})
		},
		cancel() {
			this.close()
			if (this.isUrl == true) {
				uni.switchTab({
					url: '/pages/index/index'
				})
			}

		},
		close() {
			this.$emit("changes", true);
			this.opts.isVisible = false;
		}
	}
}
